import { useMemo } from "react"

import { CopyOutlined } from "@ant-design/icons"


/**
 * 
 * @param {{font: string, category: string, type: "import"|"link"}} param0 
 * @returns 
 */
function CodeSection({font="", category="", type="import"}){

    const code = useMemo(() => {

        if (type === "import"){
            return `<style>
                        @import url('https://fonts.googleapis.com/css2?family=${font.replace(" ", "+")}');

                        .${font}-family{
                            font-family: "${font}", ${category};
                        }
                    </style>
                    `
        }else if(type === "link"){
            return `
                <link rel="preconnect" href="https://fonts.googleapis.com">
                <link rel="preconnect" href="https://fonts.gstatic.com" crossorigin>
                <link href="https://fonts.googleapis.com/css2?family=${font.replace(" ", "+")}" rel="stylesheet">

                <style>

                    .${font}-family{
                        font-family: "${font}", ${category};
                    }
                </style>

            `
        }

    }, [type, font, category])


    return (
        <div className="tw-rounded-md tw-relative tw-p-2 tw-bg-gray-100 tw-overflow-auto tw-min-h-[150px] tw-w-full tw-h-full">
            <div className="tw-absolute tw-right-2 tw-top-2">
                <button  className="hover:!tw-bg-gray-300 hover:!tw-color-black" 
								style={{outline: "none", border: "none", color: "#000", 
										backgroundColor: "transparent",
										padding: "0.5rem 0.75rem", borderRadius: "0.375rem"}}>
                    <CopyOutlined />
                </button>
            </div>
            {{code}}
        </div>
    )
}

export default CodeSection